import '../styles/Projeto.css';
import Galeria from '../components/Galeria';

function Projeto() {
    return (
        <>
            <h1 className='h1Proj'>Smart Grid</h1>
            
            <div className='paragrafo-container'>
            
                <p className='paragrafo'>
                <h2> O que ? </h2><br/>
                    Smart grid  um sistema de distribuio de energia eltrica que utiliza tecnologias avanadas de comunicao e controle para permitir uma gesto mais eficiente e inteligente da rede eltrica. Com o uso de sensores e dispositivos de monitoramento em tempo real, a smart grid  capaz de detectar problemas de energia, identificar falhas em equipamentos e at mesmo fornecer informaes sobre o consumo de energia dos usurios finais. Alm disso, a smart grid tambm possibilita a integrao de fontes de energia renovvel, como painis solares e turbinas elicas, na rede eltrica, permitindo uma melhor utilizao dos recursos energticos disponveis.
                </p>
                <img className='imgP1' src={require('../images/paragrafo1.jpg')} alt='Imagem' />
            </div>
            <h2 className='h2Proj'>Por que utilizar?</h2>
            <p className='paragrafo2'>
                A crise energtica no Brasil  um problema que tem sido enfrentado nos ltimos anos devido a uma srie de fatores. As principais causas da crise energtica no pas incluem:
            </p>
            <ul className='lista'>
                <li><b>Baixo nvel dos reservatrios de gua nas hidreltricas:</b> O sistema eltrico brasileiro  altamente dependente da energia hidreltrica, e a falta de chuvas em algumas regies tem levado a um baixo nvel dos reservatrios, reduzindo a capacidade de gerao de energia eltrica.</li>
                <li><b>Aumento do consumo de energia eltrica:</b> Com o crescimento da populao e da economia do pas, houve um aumento significativo do consumo de energia eltrica nos ltimos anos, o que tem sobrecarregado o sistema eltrico.</li>
                <li><b>Falta de investimentos em novas fontes de energia:</b> O Brasil tem um grande potencial para a gerao de energia a partir de fontes renovveis, como solar, elica e biomassa, mas os investimentos nesse setor tm sido insuficientes para suprir a demanda crescente de energia eltrica.</li>
            </ul>
            <h2 className='h2Proj'>Galeria de Imagens:</h2><br/>
            <Galeria />
        </>
    )
}

export default Projeto;
